.Main {
  margin: 0;
  color: #306135;
  font-family: url(../../fonts/RubikOne-Regular.ttf);
  font-style: normal;
  font-weight: 400;
  font-size: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 40px;
  height: 100%;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  display: flex;
}
.keykey {
  width: 13cm;
  height: 10cm;
  bottom: 10cm;
}
.player {
  position: relative;
  top: 1cm;
  height: 50cm;
  font-family: "Rubik One";
  font-style: normal;
  font-weight: 400;
  font-size: 48px;
  line-height: 59px;
  display: flex;
  align-items: center;
  width: 6cm;
  color: #614930;
  text-shadow: 4px 4px 5px rgba(0, 0, 0, 0.51);
}
.attempts{
  position: relative;
  width: 2cm;
  left: 10cm;
}
.inputSyle {
  left: 10cm;
  top: 1cm;
  border-bottom: 1px dashed black;
  width: 10cm;
  letter-spacing: 0.45em;
}
.supBiggga {
  position: absolute;
  top: 3cm;
  left: 10cm;
  width: 2cm;
}

.container {
  display: flex;
  justify-content: space-between;
  width: 5cm;
}

.cont {
  display: flex;
  flex-direction: column;
  width: 409px;
  height: 500px;
  right: 60px;
  position: relative;
  left:28cm;
  bottom: 3cm;
  
}

.in {
  height: 50px;
  display: flex;
  justify-content: space-between;
}

.inp {
  width: 55px;
  height: 50px;
  font-size: xx-large;
  font-weight: 600;
  color: aliceblue;
  border-radius: 8px;
  border: 3px solid #684208;
  border-bottom: 6px solid #684208;
}

.kb {
  height: 100%;
  margin-left: auto;
  margin-top: 30px;
  border: 13px solid rgba(63, 63, 63, 0.363);
  border-radius: 16px;
  display: grid;
  padding: 5px;
  gap: 12.5px;
  grid-template-columns: repeat(5, 65px);
  grid-template-rows: repeat(5, 55px);
}

.key {
  border-radius: 15px;
  border: none;
  background-color: #244727;
  font-size: xx-large;
  font-weight: 600;
  color: aliceblue;
}

.key:hover {
  box-shadow: 0px 0px 8px #737f91;
}
.kez:hover {
  box-shadow: 0px 0px 8px #737f91;
}
.kez {
  background-color: #244727;
  height: 55px;
  align-self: center;
  border-radius: 15px;
  border: none;
  font-size: xx-large;
  font-weight: 600;
  color: aliceblue;
  margin: 0px;
}

.orphans {
  background-color: transparent;
  border: none;
}
