:root{
  overflow: hidden;
  user-select:none;
  
}

h2 {  
  font-family: 'Rubik One';
  font-style: normal;
  font-weight: 400;
  font-size: 64px;
  line-height: 79px;
  display: flex;
  justify-content: center;

  color: #306135;

  text-shadow: 2px 2px 2px rgba(145, 145, 145, 0.51);
}

.text_style{
  font-family: 'Rubik One';
  font-style: normal;
  font-weight: 400;
  font-size: 48px;
  line-height: 59px;
  height: 1cm;
  width: 5cm;
  padding-top: 1cm;
  display: flex;
  align-items: center;
  margin-left: 35cm;
  color: #614930;

  text-shadow: 4px 4px 5px rgba(0, 0, 0, 0.51);
}
.ChatHistory{
  height: 790px;
  width: 100%;
  justify-content: center;
}

.selbtn{
  /* Auto layout */
  box-sizing: border-box;
  width: 4cm;
  height: 4cm;
  background: #D9D9D9;
  border: 5px solid #315F37;
  box-shadow: -5px 5px 6px rgba(92, 109, 135, 0.5), inset -9px 10px 17px rgba(0, 0, 0, 0.85);
  border-radius: 20px;
  overflow: hidden;

}

.selbtn:hover{
  box-shadow: -3px 0px 20px rgba(133, 153, 182, 0.863), inset -4px 5px 17px rgba(0, 0, 0, 0.85);
  background: #c9dbe9;
}

.tempo1{

  position: relative;
  max-width: 300px;
  margin-left:-85px; 
  top: -37px;
  overflow: hidden;
}

.tempo1:hover{
  transform:scale(1.2) ;
  overflow: hidden;

}


